import axios from "axios";
import { SurveyData } from "./survey";

export const generateOPIcQuestions = async (): Promise<string[]> => {
  const questions: string[] = [];

  try {
    //    
    const surveyData = await getSurvey();
    if (!surveyData) {
      throw new Error("    .");
    }

    //  
    questions.push("Let's start the interview now. Tell me about yourself");

    //   
    if (
      surveyData.occupation === "/" ||
      surveyData.occupation === "/"
    ) {
      questions.push("Can you describe your typical workday?");
      questions.push("What do you like most about your job?");
    } else if (surveyData.occupation === "/") {
      questions.push(
        "What subjects do you teach? Can you describe a typical class?"
      );
      questions.push("What's the most rewarding part of being an educator?");
    } else {
      questions.push("What kind of job would you like to have in the future?");
      questions.push(
        "What skills do you think are important for your future career?"
      );
    }

    //    
    if (surveyData.isStudent === "") {
      questions.push("What are you studying? Why did you choose this field?");
      questions.push(
        "Can you describe your typical day at school or university?"
      );
    }

    //     
    if (surveyData.recentCourse !== "  5  ") {
      questions.push(
        "Can you tell me about the most interesting course you've taken recently?"
      );
      questions.push(
        "How do you think this course will help you in the future?"
      );
    }

    //    
    questions.push(
      `You mentioned that ${surveyData.livingArrangement}. Can you describe your living space?`
    );
    questions.push("What do you like most about where you live?");

    //    
    surveyData.leisureActivities.forEach((activity) => {
      questions.push(
        `You enjoy ${activity}. Can you describe a memorable experience related to this activity?`
      );
    });

    //   
    surveyData.hobbies.forEach((hobby) => {
      questions.push(
        `Tell me about your hobby of ${hobby}. How did you get started with it?`
      );
    });

    //   
    if (surveyData.sports.includes("   ")) {
      questions.push("You mentioned you don't exercise. Can you tell me why?");
    } else {
      surveyData.sports.forEach((sport) => {
        questions.push(
          `You enjoy ${sport}. How often do you do this activity?`
        );
      });
    }

    //    
    if (surveyData.travelExperience.includes(" ")) {
      questions.push(
        "Can you describe a memorable overseas trip you've taken?"
      );
    }
    if (surveyData.travelExperience.includes(" ")) {
      questions.push(
        "What's your favorite place to visit in your country and why?"
      );
    }

    //  14  ,    .
    while (questions.length > 15) {
      questions.pop();
    }
    while (questions.length < 15) {
      questions.push(
        "Tell me about a recent challenge you faced and how you overcame it."
      );
    }

    return questions;
  } catch (error) {
    console.error("Error generating questions:", error);
    throw new Error("    .");
  }
};

//     
export const getSurvey = async (): Promise<SurveyData> => {
  try {
    const response = await axios.get(
      "https://opeak-back.onrender.com/api/survey",
      {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
          "Content-Type": "application/json",
        },
      }
    );

    if (response.status === 200) {
      console.log("Survey data retrieved successfully:", response.data);
      return response.data;
    }
    throw new Error("   .");
  } catch (error) {
    console.error("Error getting survey:", error);
    throw new Error("     .");
  }
};
