import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import { ROUTES } from "@constants/routes";
import { useAuth } from "@contexts/AuthContext";

export const Main = () => {
  const navigate = useNavigate();
  const auth = useAuth();

  if (!auth) {
    return <div>Loading...</div>;
  }

  const handleLogout = () => {
    if (window.confirm(" ?")) {
      auth.logout();
    }
  };

  return (
    <Wrapper>
      <Header>
        <Logo>O.peak</Logo>
        <NavButtons>
          {auth.isAuthenticated ? (
            <ProfileButton onClick={() => navigate(ROUTES.MY_PAGE)}>
              <ProfileIcon></ProfileIcon>
            </ProfileButton>
          ) : (
            <LoginButton onClick={() => navigate(ROUTES.LOGIN)}>
              Login
            </LoginButton>
          )}
          <MenuButton></MenuButton>
        </NavButtons>
      </Header>

      <MainContent>
        <Section>
          <SectionTitle>
            <Icon></Icon> OPIc Test
          </SectionTitle>
          <TestForm>
            <FormGroup>
              <Label></Label>
              <Select defaultValue="">
                <option></option>
              </Select>
            </FormGroup>
            <FormGroup>
              <Label></Label>
              <Select defaultValue="1">
                <option>5-1</option>
                {/* <option>2</option>
                <option>3</option> */}
              </Select>
            </FormGroup>
            <StartButton onClick={() => navigate(ROUTES.EXAM)}>
              OPIc Test Start
            </StartButton>
          </TestForm>
        </Section>

        <Section>
          <SectionTitle>
            <Icon></Icon> OPIc Tips
          </SectionTitle>
          <TipsContainer>
            <TipButton>Youtube</TipButton>
            <TipButton>Google</TipButton>
          </TipsContainer>
        </Section>

        <Section>
          <SectionTitle>
            <Icon></Icon> OPIc Test Review
          </SectionTitle>
          <ReviewList>
            {/* <ReviewItem>2024.09.04 OPIc Test Review</ReviewItem>
            <ReviewItem>2024.09.04 OPIc Test Review</ReviewItem>
            <ReviewItem>2024.09.04 OPIc Test Review</ReviewItem> */}
          </ReviewList>
        </Section>
      </MainContent>
    </Wrapper>
  );
};

