import { surveyDataState } from "@states/surveyAtoms";
import { generateOPIcQuestions } from "../../types/question";
import { ROUTES } from "@constants/routes";
import { useState, useEffect, useRef } from "react";
import { useRecoilValue } from "recoil";
import styled from "styled-components";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { useQuery, useQueryClient } from "react-query";
import { fetchSurveyDaya } from "@services/survey";

interface TimerProps {
  isRunning: boolean;
}

interface NumberItemProps {
  isActive: boolean;
  isVisited: boolean;
}

export const Exam = () => {
  const navigate = useNavigate();
  const [questions, setQuestions] = useState<string[]>([]);
  const [error, setError] = useState<string>("");
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState<number>(0);
  const [isQuestionReady, setIsQuestionReady] = useState<boolean>(false);
  const [timeRemaining, setTimeRemaining] = useState<number>(40 * 60);
  const [isTimerRunning, setIsTimerRunning] = useState<boolean>(false);

  const speechSynthesis = window.speechSynthesis;
  const speechRef = useRef<SpeechSynthesisUtterance | null>(null);
  const queryClient = useQueryClient();
  const [data, setData] = useState<any>(null);

  //   
  const videoRef = useRef<HTMLVideoElement>(null);
  const mediaRecorderRef = useRef<MediaRecorder | null>(null);
  const [isRecording, setIsRecording] = useState<boolean>(false);
  const [webcamError, setWebcamError] = useState<string>("");

  //   
  const startWebcam = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({
        video: true,
        audio: true,
      });

      if (videoRef.current) {
        videoRef.current.srcObject = stream;
      }

      // MediaRecorder 
      mediaRecorderRef.current = new MediaRecorder(stream);
      let chunks: BlobPart[] = [];

      mediaRecorderRef.current.ondataavailable = (event) => {
        if (event.data.size > 0) {
          chunks.push(event.data);
        }
      };

      mediaRecorderRef.current.onstop = () => {
        const blob = new Blob(chunks, { type: "video/webm" });
        //  blob     
        // :   
        // const formData = new FormData();
        // formData.append('video', blob, `question_${currentQuestionIndex + 1}.webm`);
        // axios.post('/api/upload-video', formData);

        chunks = []; //  
      };
    } catch (error) {
      setWebcamError("  .  .");
      console.error("Error accessing webcam:", error);
    }
  };

  //   
  const startRecording = () => {
    if (mediaRecorderRef.current && !isRecording) {
      mediaRecorderRef.current.start();
      setIsRecording(true);
    }
  };

  //   
  const stopRecording = () => {
    if (mediaRecorderRef.current && isRecording) {
      mediaRecorderRef.current.stop();
      setIsRecording(false);
    }
  };
  useEffect(() => {
    startWebcam();
    return () => {
      //     
      if (videoRef.current && videoRef.current.srcObject) {
        const tracks = (videoRef.current.srcObject as MediaStream).getTracks();
        tracks.forEach((track) => track.stop());
      }
    };
  }, []);

  //   
  const { data: surveyData, isLoading } = useQuery("survey", fetchSurveyDaya, {
    onSuccess: (data) => {
      setData(data);
    },
  });

  useEffect(() => {
    const loadQuestions = async () => {
      try {
        const generatedQuestions = await generateOPIcQuestions();
        setQuestions(generatedQuestions);
      } catch (error) {
        setError(error + "");
      }
    };

    loadQuestions();
  }, []);

  useEffect(() => {
    let timer: NodeJS.Timeout;
    if (isTimerRunning && timeRemaining > 0) {
      timer = setInterval(() => {
        setTimeRemaining((prev) => prev - 1);
      }, 1000);
    }
    return () => {
      if (timer) clearInterval(timer);
    };
  }, [isTimerRunning, timeRemaining]);

  const speakQuestion = (question: string) => {
    if (speechRef.current) {
      speechSynthesis.cancel();
    }

    const utterance = new SpeechSynthesisUtterance(question);
    utterance.lang = "en-US";
    utterance.rate = 1.0;
    speechRef.current = utterance;
    speechSynthesis.speak(utterance);
  };

  const handleStart = () => {
    setIsTimerRunning(true);
    setIsQuestionReady(true);
    startRecording(); //  
    if (questions && questions[currentQuestionIndex]) {
      speakQuestion(questions[currentQuestionIndex]);
    }
  };

  const handleNext = () => {
    if (!questions) return;

    stopRecording(); //   
    setIsTimerRunning(false);
    setIsQuestionReady(false);

    if (currentQuestionIndex < questions.length - 1) {
      setCurrentQuestionIndex((prev) => prev + 1);
      speechSynthesis.cancel();
    }
  };

  // const handleStart = () => {
  //   setIsTimerRunning(true);
  //   setIsQuestionReady(true);
  //   if (questions && questions[currentQuestionIndex]) {
  //     speakQuestion(questions[currentQuestionIndex]);
  //   }
  // };

  // const handleNext = () => {
  //   if (!questions) return;

  //   //   
  //   setIsTimerRunning(false);
  //   //     
  //   setIsQuestionReady(false);

  //   if (currentQuestionIndex < questions.length - 1) {
  //     setCurrentQuestionIndex((prev) => prev + 1);
  //     //       
  //     speechSynthesis.cancel(); //     
  //   }
  // };

  // const formatTime = (seconds: number): string => {
  //   const minutes = Math.floor(seconds / 60);
  //   const remainingSeconds = seconds % 60;
  //   return `${minutes}:${remainingSeconds.toString().padStart(2, "0")}`;
  // };

  const formatTime = (seconds: number): string => {
    const minutes = Math.floor(seconds / 60);
    const remainingSeconds = seconds % 60;
    return `${minutes.toString().padStart(2, "0")}:${remainingSeconds
      .toString()
      .padStart(2, "0")}`;
  };

  if (!questions) {
    return <div>No questions available</div>;
  }

  return (
    <ExamWrapper>
      <Header>
        <Logo>O.peak</Logo>
        <div>
          <ProfileIcon></ProfileIcon>
          <MenuButton></MenuButton>
        </div>
      </Header>

      <ExamContainer>
        <MainContent>
          {/* <VideoSection>
            <QuestionTitle> {questions[currentQuestionIndex]}</QuestionTitle>
            <TimerDisplay isRunning={isTimerRunning}>
               {formatTime(timeRemaining)}
            </TimerDisplay>
            <ButtonContainer>
              {!isQuestionReady ? (
                <ActionButton onClick={handleStart}>Start</ActionButton>
              ) : (
                <ActionButton onClick={handleNext}>Next &gt;</ActionButton>
              )}
            </ButtonContainer>
            <VideoPlaceholder />
          </VideoSection> */}
          <VideoSection>
            <QuestionTitle> {questions[currentQuestionIndex]}</QuestionTitle>
            <TimerDisplay isRunning={isTimerRunning}>
               {formatTime(timeRemaining)}
            </TimerDisplay>
            <ButtonContainer>
              {!isQuestionReady ? (
                <ActionButton onClick={handleStart}>Start</ActionButton>
              ) : (
                <ActionButton onClick={handleNext}>Next &gt;</ActionButton>
              )}
            </ButtonContainer>
            <VideoContainer>
              <Video ref={videoRef} autoPlay playsInline muted />
              {isRecording && <RecordingIndicator />}
              {webcamError && <WebcamError>{webcamError}</WebcamError>}
            </VideoContainer>
          </VideoSection>

          <NumberList>
            {Array.from({ length: 15 }, (_, i) => i + 1).map((num) => (
              <NumberItem
                key={num}
                isActive={num === currentQuestionIndex + 1}
                isVisited={num <= currentQuestionIndex + 1}
              >
                {num}
              </NumberItem>
            ))}
          </NumberList>
        </MainContent>

        <SidePanel>
          <InfoSection>
            <InfoTitle> </InfoTitle>
            <InfoContent>{questions[currentQuestionIndex]}</InfoContent>
          </InfoSection>

          <InfoSection>
            <InfoTitle> Background Survey</InfoTitle>
            <InfoContent>
              {isLoading ? (
                "  ..."
              ) : surveyData ? (
                <SurveyList>
                  {/*  */}
                  <SurveyItem>
                    {surveyData.occupation === "  " && "  "}
                  </SurveyItem>

                  {/*   */}
                  <SurveyItem>
                    {surveyData.isStudent === "" ? "" : " "}
                  </SurveyItem>

                  {/*   */}
                  <SurveyItem>{surveyData.livingArrangement}</SurveyItem>

                  {/*   */}
                  <SurveyItem>
                    <SurveySubTitle></SurveySubTitle>
                    <BulletList>
                      {surveyData.leisureActivities?.map((activity: string) => (
                        <BulletItem key={activity}>{activity}</BulletItem>
                      ))}
                    </BulletList>
                  </SurveyItem>

                  {/* / */}
                  <SurveyItem>
                    <BulletList>
                      {surveyData.hobbies?.map((hobby: string) => (
                        <BulletItem key={hobby}>{hobby}</BulletItem>
                      ))}
                    </BulletList>
                  </SurveyItem>

                  {/*  */}
                  <SurveyItem>
                    <BulletList>
                      {surveyData.sports?.map((sport: string) => (
                        <BulletItem key={sport}>{sport}</BulletItem>
                      ))}
                    </BulletList>
                  </SurveyItem>

                  {/*   */}
                  <SurveyItem>
                    <BulletList>
                      {surveyData.travelExperience?.map((travel: string) => (
                        <BulletItem key={travel}>{travel}</BulletItem>
                      ))}
                    </BulletList>
                  </SurveyItem>
                </SurveyList>
              ) : (
                "  ."
              )}
            </InfoContent>
          </InfoSection>
        </SidePanel>
      </ExamContainer>

      <ExitButton onClick={() => navigate(ROUTES.MAIN)}></ExitButton>
    </ExamWrapper>
  );
};

const Logo = styled.div`
  font-size: 24px;
  font-weight: bold;
`;

const ProfileIcon = styled.button`
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
`;

const MenuButton = styled.button`
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
`;

const TimerDisplay = styled.div<TimerProps>`
  position: relative;
  z-index: 1;
  font-size: 2rem;
  font-weight: bold;
  color: ${(props) => (props.isRunning ? "#ff4444" : "#333")};
  margin-bottom: 20px;
`;

