import React, { useState } from "react";
import { useQuery, useMutation, useQueryClient } from "react-query";
import styled from "styled-components";
import { fetchSurveyDaya } from "@services/survey";
import { survey as surveyQuestions } from "../../../types/survey";
import axios from "axios";

// API  
async function updateSurveyData(data: any) {
  const response = await axios.post(
    "https://opeak-back.onrender.com/api/survey",
    data,
    {
      headers: {
        Authorization: `Bearer ${localStorage.getItem("token")}`,
        "Content-Type": "application/json",
      },
    }
  );
  return response.data;
}

export const SurveyManage = () => {
  const [isEditing, setIsEditing] = useState(false);
  const [editedData, setEditedData] = useState<any>(null);

  const queryClient = useQueryClient();

  //   
  const {
    data: surveyData,
    error,
    isLoading,
  } = useQuery("survey", fetchSurveyDaya, {
    onSuccess: (data) => {
      if (!editedData) {
        setEditedData(data);
      }
    },
  });

  //    mutation
  const mutation = useMutation(updateSurveyData, {
    onSuccess: () => {
      queryClient.invalidateQueries("survey");
      setIsEditing(false);
    },
  });

  if (isLoading)
    return <LoadingMessage>  ...</LoadingMessage>;
  if (error)
    return <ErrorMessage>  .</ErrorMessage>;
  if (!surveyData) return <ErrorMessage> .</ErrorMessage>;

  const handleEdit = () => {
    setIsEditing(true);
  };

  const handleSave = async () => {
    try {
      await mutation.mutateAsync(editedData);
      alert("  .");
    } catch (error) {
      alert("  .");
    }
  };

  const handleCancel = () => {
    setEditedData(surveyData);
    setIsEditing(false);
  };

  const handleChange = (questionName: string, value: string | string[]) => {
    setEditedData((prev: any) => ({
      ...prev,
      [questionName]: value,
    }));
  };

  return (
    <Container>
      <Header>
        <Title>  </Title>
        {!isEditing ? (
          <EditButton onClick={handleEdit}></EditButton>
        ) : (
          <ButtonGroup>
            <SaveButton onClick={handleSave}></SaveButton>
            <CancelButton onClick={handleCancel}></CancelButton>
          </ButtonGroup>
        )}
      </Header>

      <SurveyContent>
        {surveyQuestions.map((question) => (
          <QuestionContainer key={question.name}>
            <QuestionTitle>{question.title}</QuestionTitle>
            {isEditing ? (
              question.type === "radio" ? (
                <RadioGroup>
                  {question.options.map((option) => (
                    <RadioOption key={option}>
                      <input
                        type="radio"
                        name={question.name}
                        value={option}
                        checked={editedData[question.name] === option}
                        onChange={(e) =>
                          handleChange(question.name, e.target.value)
                        }
                      />
                      <label>{option}</label>
                    </RadioOption>
                  ))}
                </RadioGroup>
              ) : (
                <CheckboxGroup>
                  {question.options.map((option) => (
                    <CheckboxOption key={option}>
                      <input
                        type="checkbox"
                        checked={editedData[question.name]?.includes(option)}
                        onChange={(e) => {
                          const currentValues = editedData[question.name] || [];
                          let newValues;
                          if (e.target.checked) {
                            newValues = [...currentValues, option];
                          } else {
                            newValues = currentValues.filter(
                              (v: string) => v !== option
                            );
                          }
                          handleChange(question.name, newValues);
                        }}
                      />
                      <label>{option}</label>
                    </CheckboxOption>
                  ))}
                </CheckboxGroup>
              )
            ) : (
              <Answer>
                {Array.isArray(surveyData[question.name])
                  ? surveyData[question.name].join(", ")
                  : surveyData[question.name]}
              </Answer>
            )}
          </QuestionContainer>
        ))}
      </SurveyContent>
    </Container>
  );
};

// Styled Components
const Container = styled.div`
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
`;

const Header = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
`;

const Title = styled.h1`
  font-size: 24px;
  color: #333;
`;

const Button = styled.button`
  padding: 8px 16px;
  border-radius: 4px;
  border: none;
  cursor: pointer;
  font-size: 14px;
`;

const EditButton = styled(Button)`
  background-color: #2196f3;
  color: white;
`;

const SaveButton = styled(Button)`
  background-color: #4caf50;
  color: white;
  margin-right: 10px;
`;

const CancelButton = styled(Button)`
  background-color: #f44336;
  color: white;
`;

const ButtonGroup = styled.div`
  display: flex;
  gap: 10px;
`;

const LoadingMessage = styled.div`
  text-align: center;
  margin-top: 20px;
  font-size: 18px;
`;

const ErrorMessage = styled.div`
  color: red;
  text-align: center;
  margin-top: 20px;
  font-size: 18px;
`;

const QuestionContainer = styled.div`
  margin-bottom: 20px;
`;

const QuestionTitle = styled.h3`
  margin-bottom: 10px;
  color: #333;
`;

const RadioGroup = styled.div`
  display: flex;
  flex-direction: column;
  gap: 8px;
`;

const CheckboxGroup = styled.div`
  display: flex;
  flex-direction: column;
  gap: 8px;
`;

const Answer = styled.div`
  padding: 10px;
  background-color: #f5f5f5;
  border-radius: 4px;
`;

const SurveyContent = styled.div`
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 20px;
`;

const RadioOption = styled.div`
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 4px 0;

  input[type="radio"] {
    margin: 0;
  }

  label {
    cursor: pointer;
  }
`;

const CheckboxOption = styled.div`
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 4px 0;

  input[type="checkbox"] {
    margin: 0;
  }

  label {
    cursor: pointer;
  }
`;
